// Cihaz Detay Sayfası
// Bu sayfa, QR kodun yönlendirdiği ve cihazın güncel bilgilerini, notlarını ve belgelerini gösteren ana ekrandır.
// Her açılışta API'den güncel veri çeker. UI, proje kurallarına uygun olarak minimal ve modern tasarlanmıştır.

import React, { useState, useTransition, useRef } from 'react';
import { notFound } from 'next/navigation';
import { ArrowLeft, FileText, StickyNote } from 'lucide-react';
import Link from 'next/link';
import QRCodePreview from '@/components/QRCodePreview';
import { Dialog } from '@headlessui/react';

// API'den cihaz detayını, notları ve belgeleri çeken fonksiyon
async function fetchDevice(id: string) {
  const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8000'}/api/devices/${id}/`, {
    cache: 'no-store',
  });
  if (!res.ok) return null;
  return res.json();
}

// Not ekleme formu bileşeni
function AddNoteForm({ deviceId, onSuccess }: { deviceId: number, onSuccess: () => void }) {
  const [note, setNote] = useState('');
  const [loading, startTransition] = useTransition();
  const [error, setError] = useState<string | null>(null);

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    setError(null);
    startTransition(async () => {
      const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8000'}/api/devicenotes/`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ device: deviceId, note }),
      });
      if (!res.ok) {
        setError('Not eklenemedi.');
        return;
      }
      setNote('');
      onSuccess();
    });
  }

  return (
    <form onSubmit={handleSubmit} className="flex flex-col gap-2 mb-4">
      <textarea
        className="rounded-lg border border-zinc-700 bg-zinc-900/80 p-2 text-zinc-100"
        placeholder="Yeni not..."
        value={note}
        onChange={e => setNote(e.target.value)}
        required
        rows={2}
      />
      <div className="flex items-center gap-2">
        <button type="submit" disabled={loading || !note.trim()} className="px-4 py-1 rounded bg-accent text-accent-foreground font-semibold hover:bg-accent/80 transition">
          Not Ekle
        </button>
        {error && <span className="text-red-500 text-sm">{error}</span>}
      </div>
    </form>
  );
}

// Belge ekleme formu bileşeni
function AddDocumentForm({ deviceId, onSuccess }: { deviceId: number, onSuccess: () => void }) {
  const [file, setFile] = useState<File | null>(null);
  const [description, setDescription] = useState('');
  const [loading, startTransition] = useTransition();
  const [error, setError] = useState<string | null>(null);

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    setError(null);
    if (!file) return;
    const formData = new FormData();
    formData.append('device', String(deviceId));
    formData.append('file', file);
    formData.append('description', description);
    startTransition(async () => {
      const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8000'}/api/devicedocuments/`, {
        method: 'POST',
        body: formData,
      });
      if (!res.ok) {
        setError('Belge eklenemedi.');
        return;
      }
      setFile(null);
      setDescription('');
      onSuccess();
    });
  }

  return (
    <form onSubmit={handleSubmit} className="flex flex-col gap-2 mb-4">
      <input
        type="file"
        accept="application/pdf,image/*"
        onChange={e => setFile(e.target.files?.[0] || null)}
        className="rounded border border-zinc-700 bg-zinc-900/80 p-2 text-zinc-100"
        required
      />
      <input
        type="text"
        className="rounded border border-zinc-700 bg-zinc-900/80 p-2 text-zinc-100"
        placeholder="Açıklama (isteğe bağlı)"
        value={description}
        onChange={e => setDescription(e.target.value)}
      />
      <div className="flex items-center gap-2">
        <button type="submit" disabled={loading || !file} className="px-4 py-1 rounded bg-accent text-accent-foreground font-semibold hover:bg-accent/80 transition">
          Belge Ekle
        </button>
        {error && <span className="text-red-500 text-sm">{error}</span>}
      </div>
    </form>
  );
}

// Not düzenleme formu bileşeni
function EditNoteForm({ note, onClose, onSuccess }: { note: any, onClose: () => void, onSuccess: () => void }) {
  const [value, setValue] = useState(note.note);
  const [loading, startTransition] = useTransition();
  const [error, setError] = useState<string | null>(null);

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    setError(null);
    startTransition(async () => {
      const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8000'}/api/devicenotes/${note.id}/`, {
        method: 'PATCH',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ note: value }),
      });
      if (!res.ok) {
        setError('Not güncellenemedi.');
        return;
      }
      onSuccess();
      onClose();
    });
  }

  return (
    <form onSubmit={handleSubmit} className="flex flex-col gap-2">
      <textarea
        className="rounded-lg border border-zinc-700 bg-zinc-900/80 p-2 text-zinc-100"
        value={value}
        onChange={e => setValue(e.target.value)}
        required
        rows={2}
      />
      <div className="flex items-center gap-2">
        <button type="submit" disabled={loading || !value.trim()} className="px-3 py-1 rounded bg-accent text-accent-foreground font-semibold hover:bg-accent/80 transition">Kaydet</button>
        <button type="button" onClick={onClose} className="px-3 py-1 rounded bg-muted text-foreground font-semibold hover:bg-muted/80 transition">İptal</button>
        {error && <span className="text-red-500 text-sm">{error}</span>}
      </div>
    </form>
  );
}

// Belge düzenleme formu bileşeni
function EditDocumentForm({ doc, onClose, onSuccess }: { doc: any, onClose: () => void, onSuccess: () => void }) {
  const [description, setDescription] = useState(doc.description || '');
  const [loading, startTransition] = useTransition();
  const [error, setError] = useState<string | null>(null);

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    setError(null);
    startTransition(async () => {
      const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8000'}/api/devicedocuments/${doc.id}/`, {
        method: 'PATCH',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ description }),
      });
      if (!res.ok) {
        setError('Belge güncellenemedi.');
        return;
      }
      onSuccess();
      onClose();
    });
  }

  return (
    <form onSubmit={handleSubmit} className="flex flex-col gap-2">
      <input
        type="text"
        className="rounded border border-zinc-700 bg-zinc-900/80 p-2 text-zinc-100"
        value={description}
        onChange={e => setDescription(e.target.value)}
        placeholder="Açıklama"
      />
      <div className="flex items-center gap-2">
        <button type="submit" disabled={loading} className="px-3 py-1 rounded bg-accent text-accent-foreground font-semibold hover:bg-accent/80 transition">Kaydet</button>
        <button type="button" onClick={onClose} className="px-3 py-1 rounded bg-muted text-foreground font-semibold hover:bg-muted/80 transition">İptal</button>
        {error && <span className="text-red-500 text-sm">{error}</span>}
      </div>
    </form>
  );
}

// Cihaz düzenleme formu bileşeni
function EditDeviceForm({ device, onClose, onSuccess }: { device: any, onClose: () => void, onSuccess: () => void }) {
  const [form, setForm] = useState({
    name: device.name || '',
    description: device.description || '',
    brand: device.brand || '',
    model: device.model || '',
    serial: device.serial || '',
    location: device.location || '',
    status: device.status || '',
    last_maintenance: device.last_maintenance || '',
  });
  const [loading, startTransition] = useTransition();
  const [error, setError] = useState<string | null>(null);

  function handleChange(e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) {
    setForm(f => ({ ...f, [e.target.name]: e.target.value }));
  }

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    setError(null);
    startTransition(async () => {
      const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8000'}/api/devices/${device.id}/`, {
        method: 'PATCH',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(form),
      });
      if (!res.ok) {
        setError('Cihaz güncellenemedi.');
        return;
      }
      onSuccess();
      onClose();
    });
  }

  return (
    <form onSubmit={handleSubmit} className="flex flex-col gap-3 p-2">
      <input name="name" value={form.name} onChange={handleChange} required className="rounded border border-zinc-700 bg-zinc-900/80 p-2 text-zinc-100" placeholder="Cihaz Adı" />
      <textarea name="description" value={form.description} onChange={handleChange} className="rounded border border-zinc-700 bg-zinc-900/80 p-2 text-zinc-100" placeholder="Açıklama" rows={2} />
      <input name="brand" value={form.brand} onChange={handleChange} className="rounded border border-zinc-700 bg-zinc-900/80 p-2 text-zinc-100" placeholder="Marka" />
      <input name="model" value={form.model} onChange={handleChange} className="rounded border border-zinc-700 bg-zinc-900/80 p-2 text-zinc-100" placeholder="Model" />
      <input name="serial" value={form.serial} onChange={handleChange} className="rounded border border-zinc-700 bg-zinc-900/80 p-2 text-zinc-100" placeholder="Seri No" />
      <input name="location" value={form.location} onChange={handleChange} className="rounded border border-zinc-700 bg-zinc-900/80 p-2 text-zinc-100" placeholder="Konum" />
      <input name="status" value={form.status} onChange={handleChange} className="rounded border border-zinc-700 bg-zinc-900/80 p-2 text-zinc-100" placeholder="Durum" />
      <input name="last_maintenance" value={form.last_maintenance || ''} onChange={handleChange} type="date" className="rounded border border-zinc-700 bg-zinc-900/80 p-2 text-zinc-100" placeholder="Son Bakım" />
      <div className="flex items-center gap-2 mt-2">
        <button type="submit" disabled={loading} className="px-4 py-1 rounded bg-accent text-accent-foreground font-semibold hover:bg-accent/80 transition">Kaydet</button>
        <button type="button" onClick={onClose} className="px-4 py-1 rounded bg-muted text-foreground font-semibold hover:bg-muted/80 transition">İptal</button>
        {error && <span className="text-red-500 text-sm">{error}</span>}
      </div>
    </form>
  );
}

// Ana cihaz detay bileşeni
export default async function DeviceDetailPage({ params }: { params: { id: string } }) {
  // params.id ile cihaz ID'si alınır
  const device = await fetchDevice(params.id);
  if (!device) return notFound();

  // Sayfa içi veri güncelleme için refresh fonksiyonu
  const refresh = () => window.location.reload();

  const [editOpen, setEditOpen] = useState(false);
  const [editNoteId, setEditNoteId] = useState<number | null>(null);
  const [editDocId, setEditDocId] = useState<number | null>(null);
  const printRef = useRef<HTMLDivElement>(null);

  // Silme fonksiyonları
  async function handleDeleteNote(id: number) {
    if (!confirm('Not silinsin mi?')) return;
    await fetch(`${process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8000'}/api/devicenotes/${id}/`, { method: 'DELETE' });
    refresh();
  }
  async function handleDeleteDoc(id: number) {
    if (!confirm('Belge silinsin mi?')) return;
    await fetch(`${process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8000'}/api/devicedocuments/${id}/`, { method: 'DELETE' });
    refresh();
  }

  // Sadece QR ve özet alanını yazdırmak için fonksiyon
  function handlePrint() {
    if (!printRef.current) return;
    const printContents = printRef.current.innerHTML;
    const win = window.open('', '', 'width=600,height=800');
    if (!win) return;
    win.document.write(`
      <html>
        <head>
          <title>QR Kod Çıktısı</title>
          <style>
            body { font-family: sans-serif; background: #fff; color: #111; margin: 0; padding: 24px; }
            .qr-box { display: flex; flex-direction: column; align-items: center; gap: 16px; border: 1px solid #ddd; border-radius: 16px; padding: 24px; max-width: 340px; margin: 0 auto; }
            .qr-box h1 { font-size: 1.5rem; margin: 0; }
            .qr-box p { font-size: 1rem; color: #444; margin: 0; }
            img { background: #fff; border-radius: 8px; border: 1px solid #eee; }
          </style>
        </head>
        <body>
          <div class="qr-box">${printContents}</div>
        </body>
      </html>
    `);
    win.document.close();
    win.focus();
    win.print();
    setTimeout(() => win.close(), 1000);
  }

  return (
    <div className="max-w-3xl mx-auto py-10 px-4">
      {/* QR Kod ve cihaz kısa özeti + Yazdır butonu */}
      <div className="flex flex-col md:flex-row items-center gap-6 mb-8">
        <div ref={printRef} className="flex flex-col items-center gap-2">
          <QRCodePreview
            url={`${process.env.NEXT_PUBLIC_PANEL_URL || 'http://localhost:3000'}/devices/${device.id}`}
            size={160}
          />
          <h1 className="text-2xl font-bold mt-2 mb-1 text-center">{device.name}</h1>
          <p className="text-zinc-500 text-center mb-1">{device.description}</p>
        </div>
        <div className="flex-1 flex flex-col items-start">
          <button onClick={handlePrint} className="mb-4 px-4 py-2 rounded bg-muted text-foreground font-semibold hover:bg-muted/80 transition border border-zinc-700">
            Yazdır / PDF olarak indir
          </button>
          <div className="text-sm text-zinc-500 mb-2">Bu QR kodu cihaza yapıştırın. Okutulduğunda cihazın güncel detay sayfasına yönlendirir.</div>
          <button onClick={() => setEditOpen(true)} className="px-4 py-1 rounded bg-accent text-accent-foreground font-semibold hover:bg-accent/80 transition mt-2">Düzenle</button>
        </div>
      </div>

      {/* Geri Dön Butonu */}
      <Link href="/devices" className="inline-flex items-center gap-2 text-muted-foreground hover:text-accent mb-6">
        <ArrowLeft size={18} /> Cihaz Listesine Dön
      </Link>

      {/* Cihaz Temel Bilgileri */}
      <div className="bg-zinc-900/80 dark:bg-zinc-800/80 rounded-2xl p-8 shadow-lg mb-8">
        <h1 className="text-3xl md:text-4xl font-bold mb-2 tracking-tight">{device.name}</h1>
        <p className="text-lg text-zinc-400 mb-4">{device.description}</p>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-base">
          <div><span className="font-semibold">Marka:</span> {device.brand || '-'}</div>
          <div><span className="font-semibold">Model:</span> {device.model || '-'}</div>
          <div><span className="font-semibold">Seri No:</span> {device.serial || '-'}</div>
          <div><span className="font-semibold">Konum:</span> {device.location || '-'}</div>
          <div><span className="font-semibold">Durum:</span> {device.status || '-'}</div>
          <div><span className="font-semibold">Son Bakım:</span> {device.last_maintenance || '-'}</div>
        </div>
      </div>

      {/* Notlar Bölümü */}
      <section className="mb-8">
        <h2 className="flex items-center gap-2 text-2xl font-semibold mb-4"><StickyNote size={20}/> Notlar</h2>
        {/* Not ekleme formu */}
        <AddNoteForm deviceId={device.id} onSuccess={refresh} />
        {device.notes && device.notes.length > 0 ? (
          <ul className="space-y-2">
            {device.notes.map((note: any) => (
              <li key={note.id} className="bg-zinc-800/80 rounded-lg p-4 text-zinc-200 border border-zinc-700 relative">
                <div className="text-base">{note.note}</div>
                <div className="text-xs text-zinc-400 mt-1">{new Date(note.created_at).toLocaleString()}</div>
                <div className="absolute top-2 right-2 flex gap-1">
                  <button onClick={() => setEditNoteId(note.id)} className="text-xs px-2 py-1 rounded bg-muted text-foreground hover:bg-muted/80">Düzenle</button>
                  <button onClick={() => handleDeleteNote(note.id)} className="text-xs px-2 py-1 rounded bg-red-600 text-white hover:bg-red-700">Sil</button>
                </div>
                {/* Not düzenleme modalı */}
                {editNoteId === note.id && (
                  <Dialog open={true} onClose={() => setEditNoteId(null)} className="fixed z-50 inset-0 flex items-center justify-center">
                    <Dialog.Overlay className="fixed inset-0 bg-black/40 backdrop-blur-sm" />
                    <div className="relative bg-zinc-900 dark:bg-zinc-800 rounded-2xl shadow-2xl p-6 w-full max-w-lg mx-auto">
                      <h2 className="text-xl font-bold mb-4">Notu Düzenle</h2>
                      <EditNoteForm note={note} onClose={() => setEditNoteId(null)} onSuccess={refresh} />
                    </div>
                  </Dialog>
                )}
              </li>
            ))}
          </ul>
        ) : (
          <div className="text-zinc-500">Henüz not eklenmemiş.</div>
        )}
      </section>

      {/* Belgeler Bölümü */}
      <section>
        <h2 className="flex items-center gap-2 text-2xl font-semibold mb-4"><FileText size={20}/> Belgeler</h2>
        {/* Belge ekleme formu */}
        <AddDocumentForm deviceId={device.id} onSuccess={refresh} />
        {device.documents && device.documents.length > 0 ? (
          <ul className="space-y-2">
            {device.documents.map((doc: any) => (
              <li key={doc.id} className="bg-zinc-800/80 rounded-lg p-4 flex items-center gap-4 border border-zinc-700 relative">
                <a href={doc.file} target="_blank" rel="noopener noreferrer" className="text-accent underline font-medium">
                  {doc.description || doc.file.split('/').pop()}
                </a>
                <span className="text-xs text-zinc-400 ml-auto">{new Date(doc.uploaded_at).toLocaleString()}</span>
                <div className="absolute top-2 right-2 flex gap-1">
                  <button onClick={() => setEditDocId(doc.id)} className="text-xs px-2 py-1 rounded bg-muted text-foreground hover:bg-muted/80">Düzenle</button>
                  <button onClick={() => handleDeleteDoc(doc.id)} className="text-xs px-2 py-1 rounded bg-red-600 text-white hover:bg-red-700">Sil</button>
                </div>
                {/* Belge düzenleme modalı */}
                {editDocId === doc.id && (
                  <Dialog open={true} onClose={() => setEditDocId(null)} className="fixed z-50 inset-0 flex items-center justify-center">
                    <Dialog.Overlay className="fixed inset-0 bg-black/40 backdrop-blur-sm" />
                    <div className="relative bg-zinc-900 dark:bg-zinc-800 rounded-2xl shadow-2xl p-6 w-full max-w-lg mx-auto">
                      <h2 className="text-xl font-bold mb-4">Belgeyi Düzenle</h2>
                      <EditDocumentForm doc={doc} onClose={() => setEditDocId(null)} onSuccess={refresh} />
                    </div>
                  </Dialog>
                )}
              </li>
            ))}
          </ul>
        ) : (
          <div className="text-zinc-500">Henüz belge eklenmemiş.</div>
        )}
      </section>

      {/* Düzenleme modalı */}
      <Dialog open={editOpen} onClose={() => setEditOpen(false)} className="fixed z-50 inset-0 flex items-center justify-center">
        <Dialog.Overlay className="fixed inset-0 bg-black/40 backdrop-blur-sm" />
        <div className="relative bg-zinc-900 dark:bg-zinc-800 rounded-2xl shadow-2xl p-6 w-full max-w-lg mx-auto">
          <h2 className="text-xl font-bold mb-4">Cihazı Düzenle</h2>
          <EditDeviceForm device={device} onClose={() => setEditOpen(false)} onSuccess={refresh} />
        </div>
      </Dialog>
    </div>
  );
} 